import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { HomePageComponent } from './home-page/home-page.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { AppRoutingModule } from './app-routing.module';
import { EventsPageComponent } from './events-page/events-page.component';
import { ToursPageComponent } from './tours-page/tours-page.component';
import { GalleryPageComponent } from './gallery-page/gallery-page.component';
import { VideosPageComponent } from './videos-page/videos-page.component';
import { BlogPageComponent } from './blog-page/blog-page.component';
import { ArmeniaPageComponent } from './armenia-page/armenia-page.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { FooterComponent } from './footer/footer.component';
import { InfiniteCarouselComponent } from './infinite-carousel/infinite-carousel.component';

import { SelectModule } from 'ng-select';
import { UICarouselModule } from 'ui-carousel';


@NgModule({
  declarations: [
    AppComponent,
    HomePageComponent,
    AboutUsComponent,
    EventsPageComponent,
    ToursPageComponent,
    GalleryPageComponent,
    VideosPageComponent,
    BlogPageComponent,
    ArmeniaPageComponent,
    ContactUsComponent,
    FooterComponent,
    InfiniteCarouselComponent
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    UICarouselModule,
    SelectModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



